  pointer-events: none;
}
.promo-video button::before {
  content: "";
  display: block;
  position: absolute;
  margin-left: 8px;
  top: 50%;
  left: 50%;
  border: 10px solid transparent;
  border-left: 15px solid #484848;
  transform: translate(-50%, -50%);
}

.video iframe {
  width: 100%;
  height: 100%;
  border: none;
}

.header {
  position: absolute;
  top: 0;
  width: 100%;
}

.site-nav {
  padding-top: 35px;
}
@media (max-width: 767px) {
  .site-nav {
    padding-top: 18px;
  }
}

.site-nav__list {
  display: grid;
  grid-template-columns: 1fr min-content 1fr;
  align-items: center;
}
@media (max-width: 767px) {
  .site-nav__list {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: min-content min-content;
    row-gap: 4px;
  }
}

.site-nav__item--logo {
  grid-column: 2/3;
  grid-row: 1/2;
  width: 137px;
}
@media (max-width: 767px) {
  .site-nav__item--logo {
    padding-right: 0;
    grid-row: 1/3;
  }
}

.site-nav__item--phone {
  grid-column: 1/2;
  grid-row: 1/2;
}
@media (max-width: 767px) {
  .site-nav__item--phone {
    order: 0;
    grid-column: 2/3;
    grid-row: 1/2;
    text-align: end;
  }
}

.site-nav__item--button {
  grid-column: 3/4;
  grid-row: 1/2;
}
@media (max-width: 767px) {
  .site-nav__item--button {
    grid-column: 2/3;
    grid-row: 2/3;
    text-align: end;
  }
}

.site-nav__link {
  font-family: "FiraSans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 18px;
  font-weight: 500;
}
@media (max-width: 767px) {
  .site-nav__link {
    font-family: "FiraSans", "Arial", sans-serif;
    font-size: 14px;
    line-height: 16px;
    font-weight: 500;
  }
}

.site-nav__link--button {
  text-transform: lowercase;
  border-bottom: 1px dashed #484848;
}

.site-nav__item:nth-of-type(3n) {
