.hero {
  position: relative;
}

.hero__wrapper {
  position: relative;
  background-repeat: no-repeat;
  min-height: 880px;
  background-position-x: 50%;
  background-size: initial;
}
@media (max-width: 1023px) {
  .hero__wrapper {
    background-size: cover;
  }
}
@media (max-width: 767px) {
  .hero__wrapper {
    min-height: 635px;
    background-position: 50% 70%;
  }
}

.hero__text {
  position: absolute;
  left: 50%;
  top: 18%;
  transform: translateX(-50%);
  z-index: 5;
  width: 1018px;
  height: 340px;
}
@media (max-width: 1023px) {
  .hero__text {
    max-width: 95%;
  }
}
@media (max-width: 767px) {
  .hero__text {
    display: flex;
    align-items: center;
    justify-content: center;
    top: 27%;
    max-width: unset;
    width: 350px;
    height: 122px;
  }
}

.hero__text img {
  width: 100%;
}

.hero__image {
  position: absolute;
  left: 50%;
  bottom: -25px;
  transform: translateX(-50%);
  z-index: 10;
  width: 1366px;
  height: 906px;
}
@media (max-width: 767px) {
  .hero__image {
    width: 320px;
    height: 605px;
    bottom: 0;
  }
}

.hero__wrapper h1 {
  font-family: "FiraSans", "Arial", sans-serif;
  font-size: 24px;
  line-height: 28px;
  font-weight: 500;
  position: relative;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 540px;
  padding-bottom: 145px;
  z-index: 15;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  color: #ffffff;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12);
  max-width: 345px;
}
@media (max-width: 767px) {
  .hero__wrapper h1 {
    font-family: "FiraSans", "Arial", sans-serif;
    font-size: 20px;
    line-height: 24px;
    font-weight: 500;
    width: 100%;
    padding-top: 335px;
    padding-bottom: 255px;
    padding-left: 15px;
    padding-right: 15px;
  }
}
.hero__wrapper h1::after {
  content: "";
  position: absolute;
  width: 76px;
  height: 132px;
  left: 50%;
  bottom: -20px;
  transform: translateX(-50%);
  z-index: 15;
  background-image: url("../img/common/scroll.svg");
}
@media (max-width: 767px) {
  .hero__wrapper h1::after {
    bottom: 90px;
  }
}

.webp .hero__wrapper {
  background-image: url("../img/common/bg.webp");
}
@media (min-resolution: 2dppx) {
  .webp .hero__wrapper {
    background-image: url("../img/common/bg@2x.webp");
  }
}

.no-webp .hero__wrapper {
  background-image: url("../img/common/bg.png");
}
@media (min-resolution: 2dppx) {
  .no-webp .hero__wrapper {
    background-image: url("../img/common/bg@2x.png");
  }
}

.footer {
  padding-top: 28px;
  padding-bottom: 20px;
  border-top: 1px solid rgba(72, 72, 72, 0.2);
}
@media (max-width: 767px) {
  .footer {
    padding-top: 32px;
    padding-bottom: 32px;
  }
}

.footer__wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 20px;
  align-items: start;
}
@media (max-width: 767px) {
  .footer__wrapper {
    grid-template-columns: 1fr;
    justify-items: center;
    row-gap: 24px;
  }
}

.footer__logo {
  display: flex;
}

.footer__copyright p {
  font-family: "FiraSans", "Arial", sans-serif;
  font-size: 12px;
  line-height: 18px;
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
  color: rgba(72, 72, 72, 0.5);
}

.footer__social {
  justify-self: end;
}
@media (max-width: 767px) {
  .footer__social {
    justify-self: center;
  }
}

.social {
  display: flex;
  justify-content: end;
  gap: 20px;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  max-width: 140px;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .social {
    justify-content: start;
  }
}

.social__item {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
}

.social__link {
  display: block;
  width: 24px;
  height: 24px;
  opacity: 0.16;
  color: #000000;
}
@media (hover: hover) {
  .social__link:hover:not(.focus-visible) {
    opacity: 1;
  }
}
.social__link.focus-visible:focus {
  opacity: 1;
}
.social__link.focus-visible:active {
  opacity: 0.5;
}
.social__link:active:not(.focus-visible) {
  opacity: 0.5;
}
@media (max-width: 767px) {
  .social__link {
    width: 32px;
    height: 32px;
  }
}
.social__link svg {
  width: 24px;
  height: 24px;
}
@media (max-width: 767px) {
  .social__link svg {
    width: 32px;
    height: 32px;
  }
}

